html {
  &[dir="rtl"] {
    .swal2-popup.fwp-swal2-popup .dynamic_popup {
      &__header {
        &__image {
          @include mq($from: 250px) {
            transform: translateX(-40%);
          }
          @include mq($from: 350px) {
            transform: translateX(-50%);
          }
          @include mq($until: 250px) {
            transform: unset;
          }
        }
      }
      .popup_body {
        // button[data-type=done], .popup_step .do_recorder .audiopreview__timer {
        //   left: 10px;
        //   right: unset;
        // }
        .popup_step {
          &__header {
            flex-direction: row-reverse;
          }
          fieldset .form-wrap .form-control-label.form-control-checkbox {
            flex-direction: row-reverse;
          }
          &__info {
            fieldset .form-wrap .form-control-label.form-control-checkbox {
              flex-direction: row;
            }
          }
          
          fieldset .form-wrap .form-control-label {
            input[type=radio] + span::before, input[type=checkbox] + span::before {
              right: 0;
              left: unset;
            }
            input[type=radio]:checked + span::after, input[type=checkbox]:checked + span::after {
              left: unset;
              right: 6px;
            }
          }
        }


      }
    }
    
  }
}